.TeamSearchForm {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.TeamSearchForm-input {
  background: teal;
  border: none;
  border-top: 1px solid white;
  border-bottom: 1px solid transparent;
  color: white;
  -ms-flex-positive: 1;
      flex-grow: 1;
  font-size: 1.5rem;
  outline: none;
  padding: 0.4rem 0.6rem;
}

.TeamSearchResults-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.TeamSearchResults-item {
  border-bottom: 1px solid teal;
  padding: 1rem 0.5rem;
}

.TeamSearchResults-item:hover {
  background: rgba(0, 128, 128, 0.1);
}

.TeamSearchResult-name {
  font-weight: bold;
}

.TeamSearchResult {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.Button {
  background: teal;
  border: none;
  border-radius: 3px;
  color: rgb(238, 238, 238);
  font-size: 1rem;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.GameView {
  font-size: 1rem;
  line-height: 1.1;
  padding: 0.4rem 0.6rem;
}

.GameView:last-of-type {
  border-bottom: 1px solid teal;
}

.GameView-teams {
  font-weight: bold;
}

.p {
  padding: 0.4rem 0.6rem;
}



