.Home {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

@-webkit-keyframes pulse {
  0% { background: teal; }
  50% { background: purple; }
  100% { background: teal; }
}

@keyframes pulse {
  0% { background: teal; }
  50% { background: purple; }
  100% { background: teal; }
}

.Home.u-loading > .Home-top-bar {
  -webkit-animation: pulse 5s infinite;
          animation: pulse 5s infinite;
}

.Home-top-bar {
  -ms-flex-align: stretch;
      align-items: stretch;
  background: teal;
  color: white;
  display: -ms-flexbox;
  display: flex;
  height: 3rem;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0;
  padding: 0;
  -webkit-transition: background 500ms;
  -o-transition: background 500ms;
  transition: background 500ms;
  width: 100%;
}

.Home-title {
  -ms-flex-item-align: center;
      align-self: center;
  font-size: 1.5rem;
}

.Home-left-action,
.Home-right-action,
.Home-spacer {
  display: -ms-flexbox;
  display: flex;
  width: 100px;
}

.Home-right-action > button,
.Home-left-action > button {
  -ms-flex-item-align: stretch;
      align-self: stretch;
  background: none;
  border: none;
  color: white;
  font-size: 1rem;
  margin: 0;
  outline: none;
}

.Home-left-action {
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.Home-right-action {
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.HomeTeam-name {
  display: -ms-flexbox;
  display: flex;
  font-size: 1.5rem;
  -ms-flex-pack: justify;
      justify-content: space-between;
  line-height: 1;
  margin: 0;
  padding: 1.25rem 0.6rem;
}

.HomeTeam-name > .Button {
  font-size: 0.75rem;
  padding: 0.3rem 0.6rem;
}

.EditTeam {
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid teal;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 1rem 0.6rem;
}

.EditTeam-name {
  font-size: 1rem;
}



